<template>
  <!-- 剪裁图片组件 -->
  <van-popup
    class="bg-tran image-cropper"
    v-model="visible"
    closeable
    position="top"
    :lock-scroll="false"
    :style="{ height: '100%' }"
  >
    <div class="flex-column-center height100">
      <vueCropper
        ref="imageCropper"
        :img="url"
        :outputSize="option.outputSize"
        :outputType="option.outputType"
        :info="option.info"
        :full="option.full"
        :autoCropWidth="option.autoCropWidth"
        :autoCropHeight="option.autoCropHeight"
        :canMove="option.canMove"
        :canMoveBox="option.canMoveBox"
        :original="option.original"
        :autoCrop="option.autoCrop"
        :fixed="option.fixed"
        :fixedNumber="option.fixedNumber"
        :centerBox="option.centerBox"
        :infoTrue="option.infoTrue"
        :fixedBox="option.fixedBox"
        :high="option.high"
        :mode="option.mode"
      ></vueCropper>
      <van-col span="24" class="font14 col-white">
        <van-col span="8" class="p-2" @click="onCancel"
          ><span>取消</span></van-col
        >
        <van-col span="8" class="p-2 text-center" @click="rotateImage"
          ><span class="font18"><van-icon name="replay" /></span
        ></van-col>
        <van-col span="8" class="p-2 text-right" @click="onConfirm"
          ><span>确定<i class="el-icon-loading" v-if="loading"></i></span
        ></van-col>
      </van-col>
    </div>
  </van-popup>
</template>
<script>
import { VueCropper } from "vue-cropper";
import axios from "axios";
// const COS = require('cos-js-sdk-v5');

export default {
  components: {
    VueCropper,
  },
  props: {
    visible: Boolean, //控制是否显示
    url: String, //裁剪的图片
  },
  data() {
    return {
      loading: false,
      option: {
        outputSize: 0.8,
        info: false, // 裁剪框的大小信息
        outputType: "jpeg", // 裁剪生成图片的格式
        canScale: false, // 图片是否允许滚轮缩放
        autoCrop: true, // 是否默认生成截图框
        autoCropWidth: window.innerWidth - 100 + "px", // 默认生成截图框宽度
        autoCropHeight: window.innerWidth - 100 + "px", // 默认生成截图框高度
        high: true, // 是否按照设备的dpr 输出等比例图片
        fixedBox: true, // 固定截图框大小 不允许改变
        fixed: true, // 是否开启截图框宽高固定比例
        fixedNumber: [1, 1], // 截图框的宽高比例
        full: true, // 是否输出原图比例的截图
        canMoveBox: false, // 截图框能否拖动
        original: false, // 上传图片按照原始比例渲染
        centerBox: false, // 截图框是否被限制在图片里面
        infoTrue: false, // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
        mode: "100% auto", // 图片默认渲染方式
      },
    };
  },
  methods: {
    rotateImage() {
      //旋转图片
      this.$refs.imageCropper.rotateRight();
    },
    onCancel() {
      //取消
      this.$emit("cancel");
    },
    onConfirm() {
      //确定
      if (this.loading) {
        return;
      }
      this.loading = true;
      this.$refs.imageCropper.getCropBlob((file) => {
        console.log(file);
        //此时可以拿file进行文件上传了，以下为腾讯云COS的上传demo，可以改为自己的上传方式
        this.handleChange(file).then(
          (data) => {
            console.log(file);
            //上传成功 执行回调
            this.$emit("success", file);
            this.loading = false;
            //请求接口，更新头像操作
          },
          (error) => {
            this.loading = false;
          }
        );
      });
    },
    async handleChange(file) {
      //文件上传
      let key = "wUbxngjGDBUEsS-kulRcXh2Jp-9dXkVM";
      let secret = "gxcRJD0wqwVElf2OemeFMLWqLIo9ywNR";
      let image_base64 = file.content;
      let formData = new FormData();
      formData.append("api_key", key);
      formData.append("api_secret", secret);
      formData.append("image_base64", image_base64);
      let scan = await axios({
        url: "https://api-cn.faceplusplus.com/imagepp/v2/generalocr",
        method: "post",
        data: formData,
      });
      console.log(scan);
      
    },
  },
};
</script>
<style>
.font14 {
  color: red;
}
.image-cropper .height100 {
  height: 100vh;
}
.image-cropper .flex-column-center {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}
.image-cropper .vue-cropper {
  background: #000;
}
.image-cropper .vue-cropper .cropper-view-box {
  outline: 1px solid #fff !important;
  outline-color: #fff !important;
}
.image-cropper .van-col {
  height: 50px;
  line-height: 50px;
  margin-bottom: 30px;
}
.image-cropper .van-col .el-icon-loading {
  margin-left: 5px;
}
</style>
